.nav {
  margin: 0;
  padding: 0; }

.nav {
  list-style: none; }

.nav {
  position: relative; }
  @media (max-width: 479px) {
    .nav {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center; } }
  @media (min-width: 480px) and (max-width: 839px) {
    .nav {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center; } }
  @media (min-width: 840px) {
    .nav {
      display: table;
      height: 48px;
      margin: 0 auto; } }
  @media (max-width: 479px) {
    .nav__item {
      width: 100%;
      min-width: 80px;
      max-width: 168px; } }
  @media (min-width: 480px) and (max-width: 839px) {
    .nav__item {
      width: 100%;
      min-width: 80px;
      max-width: 168px; } }
  @media (min-width: 840px) {
    .nav__item {
      display: table-cell;
      vertical-align: middle; } }



